
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
  width: 100%;
}
a {
  text-decoration: none;
}
a:focus,
a:hover {
  color: #23527c;
  text-decoration: underline;
}
button {
  outline: none !important;
}
@media screen and (max-width: 750px) {
.main {
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.btn {
    position: fixed;
    top: 15px;
    right: 15px;
    width: 40px;
    height: 40px;
    line-height: 40px;
    padding: 0;
    border: 0;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 5px;
    outline: none;
    font-weight: bold;
    font-size: 17px;
    z-index: 1000;
}
.iconfont {
    color: #fff;
    height: 100%;
    width: 100%;
    font-size: 20px;
}
.blank {
    display: none;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
}
.me {
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
    transition: .6s;
}
.me.active {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
}
.view-down {
    height: 100%;
    width: 100%;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    overflow: hidden;
    transition: .4s;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
.view-down.active {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
}
.view-up {
    height: 100%;
    width: 100%;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    transition: .4s;
    overflow: hidden;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
}
.view-up.active {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
}
}
@media screen and (min-width: 970px) {
.main {
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.btn {
    position: fixed;
    top: 30px;
    right: 15px;
    width: 40px;
    height: 40px;
    line-height: 40px;
    padding: 0;
    border: 0;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 3px;
    outline: none;
    font-weight: bold;
    font-size: 17px;
    z-index: 1000;
}
.iconfont {
    color: #fff;
    height: 100%;
    width: 100%;
    font-size: 20px;
}
.view-down {
    height: 100vh;
    width: 300px;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    overflow: hidden;
    transition: .4s;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    z-index: 1000;
}
.view-down.active {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
}
.view-up {
    height: 100%;
    width: 300px;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    transition: .4s;
    overflow: hidden;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    z-index: 1000;
}
.view-up.active {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
}
.blank {
    display: none;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 500;
    overflow: hidden;
}
.blank.active {
    display: block;
}
}



